.residence .section1 .content,
.residence .section5 .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.residence .section1 .content .title,
.residence .section5 .content .title {
    font-size: 24px;
    line-height: 35px;
    margin-bottom: 20px;
    font-weight: 300;
}

.residence .section1 .content .desc,
.residence .section5 .content .desc {
    font-size: 14px;
    line-height: 24px;
}

.residence .section1 .content .button,
.residence .section5 .content .button {
    margin-top: 24px;
}

/* Section 2 */
.residence .section2 {
    font-size: 14px;
    color: black;
    background-color: white;
}

.residence .section2 .content {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.residence .section2 .content .intros {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 120px 0 20px;
    color: rgba(0, 0, 0, .7);
}

.residence .section2 .content .intros .desc {
    min-height: 24px;
    line-height: 24px;
}

.residence .section2 .content .images {
    width: 100%;
    overflow: hidden;
    padding: 0 60px 50px;
}


/* Section 3, Section 4, Section 6 */
.residence.container .section3,
.residence.container .section4,
.residence.container .section5,
.residence.container .section6,
.residence.container .section7{
    background-color: #212020;
}

.residence.container .section6,
.residence.container .section3{
    justify-content: flex-end;
    padding-left: 480px;
}
.residence.container .section4{
    justify-content: flex-start;
    padding-right: 480px;
}

.residence .section6 .h6-slideitem,
.residence .section4 .h4-slideitem,
.residence .section3 .h3-slideitem{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.residence .section6 .h6-slideitem > img,
.residence .section4 .h4-slideitem > img,
.residence .section3 .h3-slideitem > img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1);
}

.residence .section6 .h6-slideitem img.active,
.residence .section4 .h4-slideitem img.active,
.residence .section3 .h3-slideitem img.active {
    animation: zoomtofit 6.5s ease-out;
}

.residence .section6 .h6-pagination,
.residence .section4 .h4-pagination,
.residence .section3 .h3-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-bottom: 36px;
}

.residence .section6 .h6-content,
.residence .section4 .h4-content,
.residence .section3 .h3-content{
    position: absolute;
    left: 120px;
    top: 0;
    bottom: 80px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 300px;
}

.residence .section4 .h4-content{
    left: auto;
    right: 120px;
}

.residence .section3 .h3-content .tips,
.residence .section4 .h4-content .tips,
.residence .section6 .h6-content .tips {
    position: absolute;
    bottom: 0;
    font-size: 12px;
    line-height: 2;
    opacity: .8;
}

.residence .section3 .h3-content .tips .tip,
.residence .section4 .h4-content .tips .tip,
.residence .section6 .h6-content .tips .tip {
    white-space: pre-wrap;
}

.residence .section6 .h6-content .title,
.residence .section4 .h4-content .title,
.residence .section3 .h3-content .title{
    font-size: 22px;
    padding-bottom: 20px;
    width: 300px;
    font-weight: 500;
}

.residence .section6 .h6-content .desc,
.residence .section4 .h4-content .desc,
.residence .section3 .h3-content .desc{
    font-size: 14px;
    line-height: 24px;
    min-height: 24px;
    white-space: pre-wrap;
}

.residence.container .section5 {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    padding-bottom: 50px;
}
.residence .section5 .swiper-imagesbar{
    background-color: rgba(255, 255, 255, .2);
}
.residence .section5 .swiper-imagesbar .bar{
    background-color: rgba(255, 255, 255, .5);
}

.residence.container .section7{
    padding-top: 140px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    overflow: hidden;
}

.residence .section7 .title{
    font-size: 22px;
    margin-bottom: 50px;
    text-align: center;
}

.residence .section7 .h7-content{
    margin: 0 160px;
}

.residence .section7 .toggle-item{
    margin-bottom: 26px;
}
.residence .section7 .toggle-item.hidden .toggle-body{
    height: 0;
    opacity: 0;
    pointer-events: none;
}

.residence .section7 .toggle-item .toggle-title{
    height: 48px;
    display: flex;
    font-size: 16px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    cursor: pointer;
    opacity: .6;
    transition: opacity .3s ease-out;
}
.residence .section7 .toggle-item .toggle-btn{
    width: 24px;
    height: 24px;
}
.residence .section7 .toggle-item .toggle-title:hover{
    opacity: 1;
}

.residence .section7 .toggle-item .toggle-body{
    font-size: 14px;
    line-height: 24px;
    padding-top: 12px;
    color: rgba(255, 255, 255, .7);
    /* overflow: hidden; */
}
.residence .section7 .toggle-item .toggle-body.grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 12px;
    column-gap: 65px;
}

.residence .section7 .toggle-item .list-item.hover{
    position: relative;
    text-decoration: underline;
}
.residence .section7 .toggle-item .list-item.hover .tooltip-pop{
    position: absolute;
    opacity: 0;
    z-index: 1;
    padding: 12px 25px;
    font-size: 14px;
    color: rgba(255, 255, 255, .7);
    pointer-events: none;
    transition: opacity .3s ease-out;
    bottom: 35px;
    background-color: #242323;
    border: 1px solid rgba(255, 255, 255, .1);
}
.residence .section7 .toggle-item .list-item.hover .tooltip-pop .arrow{
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: #242323;
    border-right: 1px solid rgba(255, 255, 255, .1);
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    transform: rotate(45deg);
    bottom: -5px;
}
.residence .section7 .toggle-item .list-item.hover .tooltip-trigger:hover{
    cursor: pointer;
}
.residence .section7 .toggle-item .list-item.hover .tooltip-trigger:hover + .tooltip-pop{
    opacity: 1;
    pointer-events: auto;
}

/* animations */
.residence .section2 .content .intros {
    opacity: 0;
    transform: translateY(50px);
    transition: transform .7s ease-out 1.2s, opacity .7s ease-out 1.2s;
}

.residence .section2 .content .intros.animate {
    opacity: 1;
    transform: translateY(0);
}

.residence .section3 .h3-content,
.residence .section6 .h6-content {
    opacity: 0;
    transform: translateX(-100px);
    transition: transform .7s ease-out 1.2s, opacity .7s ease-out 1.2s;
}

.residence .section3 .h3-content.animate,
.residence .section6 .h6-content.animate {
    opacity: 1;
    transform: translateX(0);
}

.residence .section4 .h4-content {
    opacity: 0;
    transform: translateX(100px);
    transition: transform .7s ease-out 1.2s, opacity .7s ease-out 1.2s;
}

.residence .section4 .h4-content.animate {
    opacity: 1;
    transform: translateX(0);
}

.swiper-modal{
    position: fixed;
    inset: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, .9);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity .3s ease-out;
}
.swiper-modal.show{
    opacity: 1;
    pointer-events: auto;
}
.swiper-modal .close{
    position: absolute;
    top: 60px;
    left: 150px;
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.swiper-modal .sm-body{
    display: flex;
    align-items: center;
    /* width: 1060px; */
}

.swiper-modal .swiper-root{
    position: relative;
    padding: 0 48px;
    margin: 0 68px;
}

.swiper-modal .swiper-root .swiper-imgs{
    width: auto;
    height: auto;
}

.swiper-modal .swiper-root .swiper-wrapper{
    width: 720px;
    height: 480px;
}

.swiper-modal .swiper-root .swiper-wrapper .swiper-img{
    width: 100%;
    object-fit: cover;
}

.swiper-modal .swiper-btn-prev,
.swiper-modal .swiper-btn-next{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    padding: 12px;
    opacity: .7;
    transition: opacity .3s ease-out;
}

.swiper-modal .swiper-btn-prev { left: -12px; }
.swiper-modal .swiper-btn-next { right: -12px; }

.swiper-modal .swiper-btn-prev img,
.swiper-modal .swiper-btn-next img{
    width: 32px;
    height: 32px;
}

.swiper-modal .swiper-btn-prev:hover,
.swiper-modal .swiper-btn-next:hover{
    cursor: pointer;
    opacity: 1;
}

.swiper-modal .swiper-btn-prev.disabled,
.swiper-modal .swiper-btn-next.disabled{
    opacity: .3;
    pointer-events: none;
}

.swiper-modal .sm-content{
    font-size: 14px;
    line-height: 24px;
    opacity: .8;
}
.swiper-modal .sm-content .caption{
    margin-bottom: 24px;
}
